<template>
    <div>
        <da-manage-page>
            <div slot="button">
                <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
            </div>
            <el-form slot="search" :inline="true" :model="searchForm.formdata">
                <el-form-item>
                    <el-button type="primary" @click="getTbList">查询</el-button>
                </el-form-item>
            </el-form>
            <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

                <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
                    <template slot-scope="scope">
                        <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
                        <el-button size="small" @click="delTb(scope.row)">删除</el-button>
                    </template>
                </el-table-column>

            </da-table>
        </da-manage-page>

        <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
            <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">

                <el-form-item label="小时" prop="hour">
                    <el-input auto-complete="off" v-model="addForm.hour"></el-input>
                </el-form-item>
                <el-form-item label="价格" prop="price">
                    <el-input auto-complete="off" v-model="addForm.price"></el-input>
                </el-form-item>
                <el-form-item label="峰平谷" prop="flag">
                    <el-input auto-complete="off" v-model="addForm.flag"></el-input>
                </el-form-item>
                <el-form-item label="生效日期" prop="effectiveTime">
                    <el-date-picker v-model="addForm.effectiveTime" type="date" placeholder="选择日期"></el-date-picker>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
                <el-button @click="dialogVisible = false">取 消</el-button>
            </div>
        </da-dialog>
    </div>
</template>
<script>
export default {
  name: "priceElecHour",
  data() {
    return {
      // 查询表单
      searchForm: {
        formdata: {},
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "小时", prop: "hour", align: "left", sortable: true },
        { label: "价格", prop: "price", align: "left", sortable: true },
        { label: "峰平谷", prop: "flag", align: "left", sortable: true },
        {
          label: "生效日期",
          prop: "effectiveTime",
          align: "left",
          format: "YYYY-MM-DD",
          sortable: true
        }
      ],
      dialogVisible: false,
      addForm: { hour: "", price: "", flag: "", effectiveTime: "" },
      rules: this.eachValidate({
        hour: { maxString: "" },
        price: { maxString: "" },
        flag: { maxString: "" },
        effectiveTime: { maxString: "" }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑分时电价管理" : "新增分时电价管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/priceElecHour/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName].table
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = { hour: "", price: "", flag: "", effectiveTime: "" };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/priceElecHour", this.addForm).then(
            res => {
              if (res.data.status) {
                this.$message.success(res.data.answerMsg);
                this.dialogVisible = false;
                this.getTbList();
              } else {
                this.$message.error(res.data.answerMsg);
              }
            }
          );
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/priceElecHour", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    }
  },
  mounted() {
    this.getTbList();
  }
};
</script>



